import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./categories-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class CategoriesAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
 
categoriesForm: FormGroup;
opened: boolean = false;
catId = 0;  
pageValue: string ="1";
sortField: string ="";
sortDirection: string ="";
length = 10;
pageSize = 5;
pageSizeOptions = [5, 10, 25, 100];
pageIndex: number=1;
categories= new CategoriesData();

public categoriesparents = [];
public status ="";
msg = [];
config = new configDetails(); 
	 
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.catId).subscribe(params=>{
			if(params.catId != undefined){
				this.catId=params.catId; 
			}
		}); 
	console.log("catId=="+this.catId);
	if(this.catId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.categoriesForm = new FormGroup({			
			'catName' : new FormControl('', [ Validators.required]),
			'catParentId' : new FormControl('', [ Validators.required]),
			'catActive' : new FormControl(''),
			'catDesc' : new FormControl(''),
			'catEntryId' : new FormControl(''),
			'catEntryDate' : new FormControl(''),
			'catModifiedId' : new FormControl(''),
			'catModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get catName(){ return this.categoriesForm.get('catName')};
	 	get catParentId(){ return this.categoriesForm.get('catParentId')};
	 	get catActive(){ return this.categoriesForm.get('catActive')};
	 	get catDesc(){ return this.categoriesForm.get('catDesc')};
	    get catEntryId(){ return this.categoriesForm.get('catEntryId')};
	    get catEntryDate(){ return this.categoriesForm.get('catEntryDate')};
	    get catModifiedId(){ return this.categoriesForm.get('catModifiedId')};
	    get catModifiedDate(){ return this.categoriesForm.get('catModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<CategoriesData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer-data ',
							 			JSON.stringify({ 'catId' :this.catId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.categories = data['populatadata'][0];
																		console.log("catModifiedId=="+this.categories.catModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
	 
	   populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.categoriesparents = data['populateBranch']
			  															});
			 	  
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.categories[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.categoriesForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.catId == 0){ 
				this.categories['requestType'] = 'add';
			}else{
				this.categories['requestType'] = 'update';
				this.categories['catId'] = this.catId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/categories',
									JSON.stringify(this.categories),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  					         				this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.categoriesForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.categories);
	  }
 
  onDelete(){
		 this.categories['requestType'] = 'delete';
		this.categories['catId'] = this.catId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/categories',
									JSON.stringify(this.categories),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.categoriesForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
	
export class CategoriesData {

    catId: string;
    catName: string;
	catParentId:string;
	catEntryDate:string;
	catEntryId:string;
	catModifiedId:string;
	catModifiedDate:string;
	catActive:string="true";
	catDesc:string;
	
  
}

export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}




